<template>
    <div class="user-history">
        <div class="com-title-bar">
            <div class="box">
                <div class="back" @click="$routerBack"></div>
                <div class="title">历史记录</div>
                <div class="action">
                    <div class="button" @click="clearHandel">{{ clear.text }}</div>
                </div>
            </div>
        </div>
        <div class="data" v-if="view.display">
            <div class="tabs">
                <van-tabs v-model="tabs.active" lazy-render swipeable animated sticky color="#FFEA03" :line-width="44" :line-height="2" :border="false">
                    <van-tab title="今天" name="today">
                        <div class="empty" v-if="history.today.empty">
                            <div class="image">
                                <img src="../../../assets/user/history/empty.png" alt="">
                            </div>
                            <div class="text">暂无观看记录</div>
                        </div>
                        <div class="box">
                            <van-list v-model="history.today.loading" :finished="history.today.finished" finished-text="" @load="initToday">
                                <div class="item" v-for="(item,index) in history.today.data" :key="index">
                                    <div class="video" @click="videoHandel(item.video.uuid)">
                                        <div class="image">
                                            <img :src="item.video.thumbnail" alt="">
                                        </div>
                                        <div class="info">
                                            <div class="title">
                                                {{ item.video.title }}
                                            </div>
                                            <div class="star">
                                                <div class="ico">
                                                    <van-icon name="star" color="#FFEA03"/>
                                                </div>
                                                <div class="text">{{ item.video.star }}</div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </van-list>
                        </div>
                    </van-tab>
                    <van-tab title="七天" name="week">
                        <div class="empty" v-if="history.week.empty">
                            <div class="image">
                                <img src="../../../assets/user/history/empty.png" alt="">
                            </div>
                            <div class="text">暂无观看记录</div>
                        </div>
                        <div class="box">
                            <van-list v-model="history.week.loading" :finished="history.week.finished" finished-text="" @load="initWeek">
                                <div class="item" v-for="(item,index) in history.week.data" :key="index">
                                    <div class="video" @click="videoHandel(item.video.uuid)">
                                        <div class="image">
                                            <img :src="item.video.thumbnail" alt="">
                                        </div>
                                        <div class="info">
                                            <div class="title">
                                                {{ item.video.title }}
                                            </div>
                                            <div class="star">
                                                <div class="ico">
                                                    <van-icon name="star" color="#FFEA03"/>
                                                </div>
                                                <div class="text">{{ item.video.star }}</div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </van-list>
                        </div>
                    </van-tab>
                    <van-tab title="更早" name="early">
                        <div class="empty" v-if="history.early.empty">
                            <div class="image">
                                <img src="../../../assets/user/history/empty.png" alt="">
                            </div>
                            <div class="text">暂无观看记录</div>
                        </div>
                        <div class="box">
                            <van-list v-model="history.early.loading" :finished="history.early.finished" finished-text="" @load="initEarly">
                                <div class="item" v-for="(item,index) in history.early.data" :key="index">
                                    <div class="video" @click="videoHandel(item.video.uuid)">
                                        <div class="image">
                                            <img :src="item.video.thumbnail" alt="">
                                        </div>
                                        <div class="info">
                                            <div class="title">
                                                {{ item.video.title }}
                                            </div>
                                            <div class="star">
                                                <div class="ico">
                                                    <van-icon name="star" color="#FFEA03"/>
                                                </div>
                                                <div class="text">{{ item.video.star }}</div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </van-list>
                        </div>
                    </van-tab>
                </van-tabs>
            </div>
        </div>
    </div>
</template>

<script>
    import {Icon, Tab, Tabs, List} from 'vant';

    export default {
        components: {
            [Icon.name]: Icon,
            [Tab.name]: Tab,
            [Tabs.name]: Tabs,
            [List.name]: List
        },
        data() {
            return {
                view: {
                    display: true
                },
                tabs: {
                    active: 'today'
                },
                clear: {
                    text: '清空'
                },
                history: {
                    today: {
                        data: [],
                        loading: false,
                        finished: false,
                        empty: false,
                        page: 1
                    },
                    week: {
                        data: [],
                        loading: false,
                        finished: false,
                        empty: false,
                        page: 1
                    },
                    early: {
                        data: [],
                        loading: false,
                        finished: false,
                        empty: false,
                        page: 1
                    }
                }
            }
        },
        beforeRouteEnter(to, from, next) {
            to.meta.Load = false;
            if (from.path !== '/video/index' && to.meta.Create === true) {
                to.meta.Load = true;
            }
            next();
        },
        beforeRouteLeave(to, from, next) {
            //标识页面已经加载过
            from.meta.Create = true;
            if (to.path !== '/video/index') {
                this.view.display = false;
            }
            next();
        },
        activated() {
            if (this.$route.meta.Load) {
                setTimeout(() => {
                    this.view.display = true;
                }, 10);
                this.tabs.active = 'today';
                this.history = {
                    today: {
                        data: [],
                        loading: false,
                        finished: false,
                        empty: false,
                        page: 1
                    },
                    week: {
                        data: [],
                        loading: false,
                        finished: false,
                        empty: false,
                        page: 1
                    },
                    early: {
                        data: [],
                        loading: false,
                        finished: false,
                        empty: false,
                        page: 1
                    }
                };
            }
        },
        mounted() {
        },
        methods: {
            initToday() {
                this.$axios.get('/api/app/user/history/data', {
                    period: 0,
                    page: this.history.today.page
                })
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.history.today.page++;
                            if (this.history.today.page > result.last_page) {
                                this.history.today.finished = true;
                            }
                            this.history.today.data = this.history.today.data.concat(result.data);
                            if (this.history.today.data.length === 0) {
                                this.history.today.empty = true;
                            }
                            this.history.today.loading = false;
                        }
                    });
            },
            initWeek() {
                this.$axios.get('/api/app/user/history/data', {
                    period: 7,
                    page: this.history.week.page
                })
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.history.week.page++;
                            if (this.history.week.page > result.last_page) {
                                this.history.week.finished = true;
                            }
                            this.history.week.data = this.history.week.data.concat(result.data);
                            if (this.history.week.data.length === 0) {
                                this.history.week.empty = true;
                            }
                            this.history.week.loading = false;
                        }
                    });
            },
            initEarly() {
                this.$axios.get('/api/app/user/history/data', {
                    period: 90,
                    page: this.history.early.page
                })
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.history.early.page++;
                            if (this.history.early.page > result.last_page) {
                                this.history.early.finished = true;
                            }
                            this.history.early.data = this.history.early.data.concat(result.data);
                            if (this.history.early.data.length === 0) {
                                this.history.early.empty = true;
                            }
                            this.history.early.loading = false;
                        }
                    });
            },
            videoHandel(uuid) {
                this.$router.push('/video/index?uuid=' + uuid)
            },
            clearHandel() {
                this.$dialog.confirm({
                    title: '提示',
                    message: '确定清空所有历史记录?'
                }).then(() => {
                    this.$axios.post('/api/app/user/history/clear')
                        .then(response => {
                            let code = response.data.code;
                            if (code === 200) {
                                this.$toast.success('清空成功');
                                this.history.today.data = [];
                                this.history.today.empty = true;
                                this.history.week.data = [];
                                this.history.week.empty = true;
                                this.history.early.data = [];
                                this.history.early.empty = true;
                            }
                        })
                })
            }
        }
    }
</script>

